@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  font-family: "Quicksand", sans-serif;
  width: 100%;
}
*::selection {
  background: #4f46e5;
  color: #fff;
}
/* @media (max-width: 700px) {
  html {
    width: 100%;
    font-size: 75%;
  }
} */

/*     topbar start   */
.topbarContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #4f46e5;
  height: 60px;
  width: 100%;
  position: sticky;
  top: 0;
  padding: 10px;
  color: aliceblue;
}
.logo {
  font-size: 2rem;
  font-weight: 600;
  cursor: pointer;
}
.topbarCenter {
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
  background: #8a84ff;
}
.topbarCenter .searchInput {
  max-width: 250px;
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
}
.topbarRight {
  display: flex;
  align-items: center;
}
.topbarIconItem {
  margin: 0 10px;
  cursor: pointer;
  position: relative;
}
.topbarIconBadge {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  position: absolute;
  height: 15px;
  width: 15px;
  background: #bb0c0c;
  color: #fff;
  border-radius: 50%;
  top: -5px;
  right: -5px;
  text-align: center;
}
.topbarIcons {
  display: flex;
  align-items: center;
  padding: 5px;
}
.topbarContainer img {
  width: 2.3rem;
  height: 2.3rem;
  border-radius: 50%;
  object-fit: cover;
}

/*    topbar end */

.sidebarFriend {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.sidebarFriend img {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  object-fit: cover;
}
.sidebarFriendName {
  margin-left: 10px;
}

.sidebar {
  display: flex;
  flex: 3 1;
  height: calc(100vh - 60px);
  overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 7px;
}
::-webkit-scrollbar-track {
  background: rgb(230, 230, 230);
}
::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 3px;
}
.sidebarWrapper {
  padding: 20px;
  width: 100%;
}
.sidebarList {
  padding: 0;
  margin: 0;
  list-style: none;
}
.sidebarListItem {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  font-size: 1.25rem;
}
.sidebarIcon {
  margin-right: 15px;
}
.sidebarButton {
  width: 100px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  margin: 10px 0;
  font-weight: 600;
  background: #4f46e5;
  color: #fff;
}
.sidebarHr {
  margin: 10px 0;
}
.sidebarFriendLsit {
  padding: 0;
  margin: 0;
  list-style: none;
}

.rightbarFriend {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}
.rightbarProfileContainer {
  position: relative;
}
.rightbarProfileContainer img {
  width: 3rem;
  height: 3rem;
  object-fit: cover;
  border-radius: 50%;
}
.rightbarOnline {
  width: 0.9rem;
  height: 0.9rem;
  border-radius: 50%;
  background: green;
  position: absolute;
  bottom: 5px;
  right: -1px;
  border: 2px solid #fff;
}
.rightbarFriendName {
  margin-left: 15px;
  font-size: medium;
  font-weight: 500;
}

.rightbar {
  display: flex;
  flex: 3 1;
  overflow-y: scroll;
  height: calc(100vh - 60px);
}
.rightbarWrapper {
  width: 100%;
  padding: 20px 10px;
}
.bithdayContainer {
  display: flex;
  align-items: center;
}
.rightbarIcon {
  margin-right: 10px;
}
.birthdayText {
  font-weight: 300;
  font-size: 15px;
}
.sponsors {
  width: 80%;
  margin: 30px auto;
}
.sponsors img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
}
.rightbarTitle {
  font-weight: bold;
  margin-bottom: 15px;
}
.rightbarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.feed {
  width: 100%;
  display: flex;
  flex: 6 1;
  height: calc(100vh - 60px);
}
.feedWrapper {
  width: 100%;
  overflow-y: scroll;
  padding: 20px;
  width: 100%;
}

.share {
  width: 100%;
  height: 135px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgb(182 181 181 / 20%),
    inset 5px 5px 5px rgb(216 240 255 / 51%);
}
.shareWrapper {
  padding: 15px;
}
.shareTop {
  display: flex;
  align-items: center;
}
.shareTop img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
}
.shareInput {
  width: 85%;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: medium;
  border: none;
  background: rgba(231, 230, 230, 0.87);
}
.shareInput:focus {
  outline: none;
}
.shareHr {
  margin: 10px 0;
  background: rgba(231, 230, 230, 0.87);
}
.shareBottom {
  display: flex;
}
.shareOptions {
  align-items: center;
  justify-content: space-between;
  display: flex;
  width: 100%;
}
.shareOption {
  align-items: center;
  display: flex;
  cursor: pointer;
  padding: 10px;
  border-radius: 8px;
}
.shareOption:hover {
  background: rgb(188 164 255 / 20%);
}
.shareOptionText {
  margin-left: 5px;
  font-size: medium;
  font-weight: 600;
}
.shareButton {
  padding: 7px 15px;
  font-size: medium;
  margin-right: 10px;
  background: #4f46e5;
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
}

.post {
  width: 100%;
  margin: 25px 0;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgb(182, 181, 181, 0.2),
    inset 5px 5px 5px rgb(216, 240, 255, 0.51);
}
.postWrapper {
  padding: 20px 0;
  width: 90%;
  margin: 0 auto;
}
.postTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.postTopLeft {
  display: flex;
  align-items: center;
}
.postTopLeft img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
}
.postUserName {
  font-weight: bold;
  display: flex;
  flex-direction: column;
}
.postDate {
  font-weight: 600;
  margin-top: 5px;
  font-size: 11px;
}
.postTopIcon {
  cursor: pointer;
}
.postHr {
  margin: 10px 0;
  background: rgba(231, 230, 230, 0.87);
}
.postCenter {
  width: 90%;
  margin: 0 auto;
  height: 465px;
}
.caption p {
  width: 100%;
  margin-bottom: 10px;
  font-size: 1rem;
  font-weight: 600;
}
.postCenter img {
  border-radius: 10px;
  width: 100%;
  height: calc(100% - 30px);
  object-fit: contain;
  background: rgb(51, 50, 50);
}
.postBottom {
  width: 90%;
  margin: 5px auto;
  display: flex;
  justify-content: space-between;
}
.postBottomLeft {
  display: flex;
  align-items: center;
}
.postBottomIcon {
  cursor: pointer;
  font-size: 1.5rem;
  margin-right: 10px;
}
.postComment {
  margin-right: 10px;
}

.homeContainer {
  display: flex;
  width: 100%;
}

